import React, { Component, lazy, Suspense } from 'react';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import { withRouter } from 'react-router';
import NavBar from './components/NavBar';
import NotFound from './components/NotFound';
import Sensor from "./sensors/Sensor";
import Room from "./sensors/Room";
import Rpio from "./rpio/RpioBoard";

const Hub = withRouter(
  lazy(() => import('./hub/Hub'))
);


const Settings = withRouter(
  lazy(() => import('./hub/Settings'))
);

const Sensors = withRouter(
  lazy(() => import('./sensors/Sensors'))
);

const Rooms = withRouter(
  lazy(() => import('./sensors/Rooms'))
);

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
    };
  }

  render () {
      return (
        <BrowserRouter>
      <div className="allwidth">
          <NavBar />
            <Suspense fallback={<div>Laddar...</div>}>
              <Switch>
                <Route exact path={"/"} component={Hub} />
                <Route exact path={"/rooms"} component={Rooms} />
                <Route path={"/room/:id"} component={Room} />
                <Route exact path={"/sensors"} component={Sensors} />
                <Route path={"/sensor/:id"} component={Sensor} />
                <Route path={"/settings"} component={Settings} />
                <Route path={"/rpio"} component={Rpio} />
                <Route exact path="**" component={NotFound} />
              </Switch>
            </Suspense>
      </div>
      </BrowserRouter>
      );
  }
}

export default App;
